.form__button-update {
    margin: 0 calc((100vw - 320px)/(1280 - 320) * (36 - 22) + 22px);
    width: calc((100vw - 320px)/(1280 - 320) * (358 - 238) + 238px);
    height: calc((100vw - 320px)/(1280 - 320) * (50 - 46) + 46px);
    background-color: rgb(0, 0, 0);
    border: 0;
    padding: 0;
    color: #fff;
    font-size: calc((100vw - 320px)/(1280 - 320) * (18 - 14) + 14px);
    line-height: calc((100vw - 320px)/(1280 - 320) * (22 - 17) + 17px);
    cursor: pointer;
    position: relative;
    margin-bottom: calc((100vw - 320px)/(1280 - 320) * (37 - 25) + 25px);
    margin-top: 18px;
    transition: all .2s ease-in-out;
    border-radius: 2px;
}

.form__button-update:hover {
    opacity: .8;
}

.form__button-update:disabled {
    opacity: .2;
    background-color: #fff;
    color: black;
    border: 1px solid black;
}